.block-restaurante {
    @import '../variables/variables';
    @import '../components/media';
    @import '../components/colors';
    padding-top: 231px;
    padding-bottom: 100px;
    @include media(sm){
        padding: 50px 0;
    }
    .content{
        *:not(.encabezado){
            color: $white;
        }
        padding-bottom: 68px;
        max-width: 684px;
        width: 100%;
        margin: 0 auto;
        @include media(sm){
            padding-bottom: 40px;
        }
    }
    .image{
        max-height: 471px;
        min-height: 471px;
        height: 100%;
        picture,img{
            object-fit: cover;
            object-position: center;
        }
        @include media(sm){
            max-height: 60vh;
            min-height: 60vh;
        }
        @include media(xs){
            max-height: 40vh;
            min-height: 40vh;
        }
    }
}